import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            component: () => import('./pages/Index'),
            meta: {
                KeepAlive: true
            }
        },
        {
            path: '/doc',
            component: () => import('./pages/Doc'),
            meta: {
                KeepAlive: true
            }
        },
        {
            path: '/features',
            component: () => import('./pages/Features'),
            meta: {
                KeepAlive: true
            }
        }
    ]
})