<template>
    <div class="header">
        <div class="container">
            <div class="header-logo">
                <h1>{{siteName}}</h1>
            </div>
            <div class="header-nav">
                <div class="header-nav-item" :class="{'header-nav-active': active == index}" v-for="(item, index) in navList" :key="index" @click="active = index">
                    <router-link :to="item.url" v-if="item.url.startsWith('/')">{{item.name}}</router-link>
                    <a :href="item.url" target="_bank" v-else>{{item.name}}</a>
                </div>
            </div>
            <div class="header-right">
                <div class="header-right-item">
                    <a :href="github" target="_bank">
                        <img src="../assets/images/icon/github.png">
                        <span>Github</span>
                    </a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            active: 0,
            navList: [],
            github: '',
            siteName: ''
        }
    },
    async created(){
        let data = await axios.get('/db.json')
        this.navList = data.nav
        this.github = data.github
        this.siteName = data.name
    }
}
</script>

<style scoped>
/* header部分 */
.header{
    width:100%;
    height:60px;
    line-height: 60px;
    background: #20232a;
    color:#fff;
}
.header .container{
    display: flex;
}
.header-logo{
    margin: 0 100px 0 0;
}
.header-logo h1{
    font-style: normal;
    font-size:24px;
}
.header-nav{
    display: flex;
    color:#f9f9f9;
}
.header-nav-item{
    position: relative;
    margin: 0 20px 0 0;
    transition: all 0.2s;
}
.header-nav-item a{
    display: block;
    color:inherit;
    padding: 0 15px;
}
.header-nav-active, .header-nav-item:hover{
    color:#61dafb;
}
.header-nav-active::before{
    position: absolute;
    left:0;
    bottom:0;
    content: '';
    width:100%;
    height:4px;
    background: #61dafb;
}
.header-right{
    flex:1;
    text-align: right;
    font-size:14px;
    /* background: yellow;  */
}
.header-right-item{
    height:100%;
    display: inline-block;
    /* background: orange; */
}
.header-right a{
    position: relative;
    color:inherit;
    /* background: blue; */
}
.header-right a img{
    position: absolute;
    top:1px;
    left:0;
    /* background: red; */
}
.header-right a span{
    margin: 0 0 0 18px;
}

@media screen and (max-width:800px){
    .header .container{
        position:relative;
    }
    .header{
        padding:0 7px;
        height:50px;
        line-height: 50px;
    }
    .header-logo{
        margin:0 10px 0 0;
    }
    .header-logo h1{
        font-size:18px;
    }
    .header-nav{
        flex:1;
    }
    .header-nav-item{
        display: inline-block;
        margin:0 5px 0 0;
        font-size:14px;
    }
    .header-nav-item a{
        padding:0 5px;
    }
    .header-right{
        display: none;
    }
}
</style>
